import ReviewContainer from "./ReviewContainer";
import Header from "./Header";
import { useState } from "react";
import Landing from "./Landing";
import Why from "./Why";
import What from "./What";
import Projects from "./Projects";
import Fade from "react-reveal/Fade";
import Tada from "react-reveal/Tada";

let arrowVisible = false;
function App() {
  const [showReview, setShowReview] = useState(0);
  function showReviewBtn() {
    setShowReview(1);
    arrowVisible = true;
  }
  return (
    <>
      <Header />
      <Landing />
      <Why />
      <What />
      <Projects />
      <div className="container-fluid mt-5 mb-5">
        <div className="row mt-5 ">
          <div className="col-12 mt-5 text-center">
            <Fade top>
              <h6
                className="display-6 mt-5 mb-5"
                style={{ opacity: "0.8", fontWeight: "800" }}
              >
                Wanna know what my clients say?
              </h6>
            </Fade>
            <Tada>
              <button
                className="btn btn-dark arrowBtn mt-5 mb-5"
                onClick={showReviewBtn}
              >
                {arrowVisible ? (
                  <svg
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    className="bounce"
                  >
                    <path
                      fill="#fff"
                      d="M12 15.586L7.707 11.293 6.293 12.707 12 18.414 17.707 12.707 16.293 11.293z"
                    ></path>
                    <path
                      fill="#fff"
                      d="M17.707 7.707L16.293 6.293 12 10.586 7.707 6.293 6.293 7.707 12 13.414z"
                    ></path>
                  </svg>
                ) : (
                  "Yes"
                )}
              </button>
            </Tada>
          </div>
        </div>
      </div>
      {showReview ? <ReviewContainer /> : <></>}
    </>
  );
}

export default App;
