import React, { useState } from "react";
import sendBtn from "./send.svg";
import db from "./firebase";

function Form({ userName, userPhotoURL }) {
  const [countryda, setCountry] = useState([]);
  const [reviewda, setReview] = useState([]);
  const [status, setStatus] = useState(0);
  function sendIt() {
    db.collection("messages").add({
      id: userName,
      name: userName,
      country: countryda,
      dpURL: userPhotoURL,
      review: reviewda,
    });
    setStatus(1);
  }
  return (
    <>
      {status ? (
        <div
          className="row d-flex justify-content-center"
          style={{ marginTop: "12rem", marginBottom: "10rem" }}
        >
          <div className="col-md-4">
            <div
              className="alert-dark p-5 mb-5"
              style={{ borderRadius: "10px" }}
            >
              <h6>Thanks for your review!</h6>
            </div>
          </div>
        </div>
      ) : (
        <div className="row d-flex justify-content-center">
          <div className="col-md-6">
            <div>
              <h1
                className="mb-5 display-6"
                style={{ color: "#fff", opacity: 0.8 }}
              >
                Hi {userName}!
              </h1>
              <div className="form-floating mb-3">
                <input
                  required
                  type="text"
                  className="form-control"
                  id="floatingInput"
                  style={{
                    color: "#fff",
                    background: "#212529",
                    opacity: 0.9,
                    border: "none",
                  }}
                  onInput={(e) => setCountry(e.target.value)}
                />
                <label className="mb-5" style={{ color: "#fff", opacity: 0.5 }}>
                  Your country
                </label>
              </div>
              <div className="form-floating">
                <textarea
                  className="form-control"
                  placeholder="Leave a comment here"
                  id="floatingTextarea"
                  required
                  style={{
                    color: "#fff",
                    background: "#212529",
                    opacity: 0.9,
                    border: "none",
                    height: "180px",
                  }}
                  onInput={(e) => setReview(e.target.value)}
                ></textarea>
                <label style={{ color: "#fff", opacity: 0.5 }}>
                  Leave a short review here
                </label>
              </div>
              <button
                type="button"
                className="btn mt-5 mb-5"
                style={{
                  color: "#fff",
                  background: "#212529",
                  opacity: 0.9,
                  border: "none",
                }}
                onClick={sendIt}
              >
                <span className="mx-2">Send</span>
                <img src={sendBtn} alt="send btn" width="20px" />
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default Form;
