import googleIcon from "./google.gif";
import { useState } from "react";
import Form from "./Form";
import { auth, provider } from "./firebase";

function AddReview() {
  const [userName, setUserName] = useState(["Buddy"]);
  const [userPhotoURL, setUserPhotoURL] = useState([""]);
  function signIn() {
    auth.signInWithPopup(provider).then((results) => {
      setUserName(results.user.displayName);
      setUserPhotoURL(results.user.photoURL);
    });
  }

  return (
    <>
      <div
        className="row addReviewRow"
        style={{ marginTop: "-2px", marginBottom: "-2px" }}
      >
        <div className="col text-center mt-5">
          <h1 className="display-6 mt-5 addReviewtitle">Add review</h1>
          <p className="lead mt-5 mb-5 description">
            Google sign in is required to prevent bots adding reviews into this
            website. Don't worry {userName}. I won't sell your data.
          </p>
          {userName == "Buddy" ? (
            <button
              className="mt-3 mb-5 rounded googleLoginBtn hvr-buzz-out"
              onClick={signIn}
            >
              <img
                src={googleIcon}
                width="75px"
                className="rounded"
                alt="Google Sign In Icon"
              />
            </button>
          ) : (
            <Form userName={userName} userPhotoURL={userPhotoURL} />
          )}
        </div>
      </div>
    </>
  );
}

export default AddReview;
