import { useState, useEffect } from "react";
import db from "./firebase";
import mapIcon from "./mapIcon.svg";
import Fade from "react-reveal/Fade";
function Reviews() {
  const [users, setUsers] = useState([]);
  useEffect(() => {
    db.collection("messages").onSnapshot((snapshot) =>
      setUsers(
        snapshot.docs.map((doc) => ({
          id: doc.id,
          name: doc.data().name,
          country: doc.data().country,
          dpURL: doc.data().dpURL,
          review: doc.data().review,
        }))
      )
    );
  }, []);

  return (
    <>
      <div
        className="row reviewRow"
        style={{ marginTop: "-2px", marginBottom: "-2px" }}
      >
        <div className="col-12 mt-5 mb-5 text-center">
          <h3 className="display-5 mt-3 mb-3 reviewTitle">Reviews</h3>
        </div>
        {users.map((reviewCard) => (
          <Fade>
            <div className="col-md-6 col-xl-3 mb-2 mt-2" key={reviewCard.id}>
              <div class="alert reviewAlert" key={reviewCard.id}>
                <div className="row">
                  <div className="col-4 cus-col-xs-5 col-lg-4 d-flex justify-content-center">
                    <img
                      key={reviewCard.id}
                      src={reviewCard.dpURL}
                      className="br50"
                      alt={reviewCard.name}
                    />
                  </div>
                  <div className="col-8 cus-col-xs-7">
                    <h5 className="reviewerText mt-3">{reviewCard.name}</h5>
                    <img
                      src={mapIcon}
                      className="d-inline mapIcon"
                      width="23px"
                      alt={reviewCard.name}
                    />
                    <p className="reviewerCountryText d-inline">
                      {reviewCard.country}
                    </p>
                  </div>
                </div>
                <div className="row">
                  <div className="col-12 mt-3">
                    <p className="reviewText">{reviewCard.review}</p>
                  </div>
                </div>
              </div>
            </div>
          </Fade>
        ))}
      </div>
    </>
  );
}

export default Reviews;
